import appNav from "../../components/appNav";
import social from "../../components/social";
Vue.use(VueSession);
Vue.use(VueResource);
export default {
  name: "Authentication",
  components: {
    social,
    appNav
  },
  data() {
    const EmailPhone = (rule, value, callback) => {
      var emailregex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (value === "") {
        callback(new Error("Please enter your e-mail"));
      } else if (!emailregex.test(String(value).toLowerCase())) {
        callback(new Error("Please enter a valid email"));
      } else {
        callback();
      }
    };
    return {
      showUp: true,
      passcontinue: false,
      showUp: true,
      navtype: "Auth",
      authActive: false,
      kews: [],
      user: {
        phone: "",
        countryAbbr: "gh",
        countryCode: ""
      },
      phoneVal: false,
      authActive: false,
      showMobNav: false,
      redirect: "/dashboard",
      curYear: "",
      psw_info: "",
      setPassword: false,
      authSignUp: "authSignUp",
      current: 3,
      loading: true,
      socialPassword: "",
      social: {},
      signupValidate: {
        email: "",
        name: "",
        password: "",
        tc: [],
        timezone: "",
        phone: ""
      },
      phoneChex: {
        phone: ""
      },
      signupRuleValidate: {
        password: [
          {
            required: true,
            message: "Please fill in the password.",
            trigger: "blur"
          },
          {
            type: "string",
            min: 6,
            message: "The password length cannot be less than 6 bits",
            trigger: "blur"
          }
        ],
        tc: [
          {
            required: true,
            type: "array",
            message: "Agree to terms & conditions",
            trigger: "change"
          }
        ],
        name: [
          {
            required: true,
            message: "Please fill in your fullname.",
            trigger: "blur"
          },
          {
            type: "string",
            min: 6,
            message: "Full name length cannot be less than 6 bits",
            trigger: "blur"
          }
        ]
      },
      logValidate: {
        email: "",
        password: "",
        phone: ""
      },
      ruleValidate: {
        email: [
          {
            required: true,
            message: "Mailbox cannot be empty",
            trigger: "blur"
          },
          { type: "email", message: "Incorrect email format", trigger: "blur" }
        ],
        password: [
          {
            required: true,
            message: "Please fill in the password.",
            trigger: "blur"
          },
          {
            type: "string",
            min: 6,
            message: "The password length cannot be less than 6 bits",
            trigger: "blur"
          }
        ]
      }
    };
  },
  beforeCreate: function() {
    if (this.$session.has("usrid")) {
      let user = this.$session.get("usrid");
      this.$router.push("/dashboard");
    }
  },
  created() {
    this.defLoad();
    this.$Message.config({
      top: 80,
      duration: 10
    });
    !this.$route.query.redir
      ? (this.redirect = "/")
      : (this.redirect = this.$route.query.redir);
  },
  methods: {
    loadingSync(e) {
      const msg = this.$Message.loading({
        content: e,
        duration: 20
      });
    },
    phoneValidate() {
      const emval = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (this.signupValidate.email === "") {
        this.$Message.destroy();
        this.$Message.error("Email required");
      } else if (!emval.test(String(this.signupValidate.email).toLowerCase())) {
        this.$Message.destroy();
        this.$Message.error("Please enter a valid email");
      } else {
        this.$Message.destroy();
        this.passcontinue = true;
      }
    },
    addUser(user) {
      let url = config.createAuth;
      return Vue.http
        .post(url, user)
        .then(response => Promise.resolve(response.data))
        .catch(error => Promise.reject(error));
    },
    authUser(user) {
      let url = config.signAuth;
      return Vue.http
        .post(url, user)
        .then(response => Promise.resolve(response.data))
        .catch(error => Promise.reject(error));
    },
    upSubmit(create) {
      this.$refs[create].validate(valid => {
        if (valid) {
          this.$Message.destroy();
          this.loadingSync("Hold on a second");
          this.signupValidate.timezone = new Date()
            .toString()
            .match(/\(([A-Za-z\s].*)\)/)[1];
          this.addUser(JSON.stringify(this.signupValidate))
            .then(response => {
              this.$Message.destroy();
              this.loadingSync("Logging in");
              if (!response.error) {
                this.$session.start();
                this.$session.set("usrid", response);
                this.$router.push(this.redirect);
                this.$Message.destroy();
                this.$Message.success("logged in");
              } else if (response.error === "Account exist") {
                this.$Message.error(response.error + " please sign in");
              } else {
                this.$Message.error(response.error);
              }
            })
            .catch(errors => {
              console.log("Failed");
            });
        } else {
          this.$Message.error(
            "Oops Error, please provide valid sign up information!"
          );
        }
      });
    },
    logSubmit(auth) {
      this.$refs[auth].validate(valid => {
        if (valid) {
          this.$Message.destroy();
          this.loadingSync("Hold on a second");
          this.logValidate.timezone = new Date()
            .toString()
            .match(/\(([A-Za-z\s].*)\)/)[1];
          this.authUser(JSON.stringify(this.logValidate))
            .then(response => {
              this.$Message.destroy();
              this.loadingSync("Logging in");
              if (!response.error) {
                this.$session.start();
                this.$session.set("usrid", response);
                this.$router.push(this.redirect);
                this.$Message.destroy();
                this.$Message.success("logged in");
              } else {
                this.$Message.destroy();
                this.$Message.error(response.error);
              }
            })
            .catch(errors => {
              console.log("Failed");
            });
        } else {
          this.$Message.error(
            "Oops Error, please provide valid sign in information!"
          );
        }
      });
    },
    defLoad() {
      let d = new Date();
      this.curYear = d.getFullYear();
      this.$route.query.signUp === "true"
        ? (this.showUp = true)
        : (this.showUp = false);
      this.$route.query.signUp === "true"
        ? (document.title = "Appraisal - Create Account")
        : (document.title = "Appraisal - Sign In");
    }
  }
};
</script>

<style lang="less" scoped>
.body-sec {
  background: #eeeeee !important;
}

.body-pad {
  min-height: 90vh;
}

.body-pad h1 {
  color: #000;
}

.avatar-home {
  width: 80px !important;
  height: auto !important;
  border-radius: 100% !important;
}

.time {
  font-size: 14px;
  font-weight: bold;
}

.content-time {
  padding-left: 5px;
}

.call-to-action {
  background: #6c2bd0 !important;
  color: #fff !important;
  text-align: center !important;
}

.action-button {
  color: #fff !important;
}

@media (max-width: 450px) {
  .tag-home {
    overflow: visible !important;
    height: 60px !important;
  }
}

.btn-sturta {
  padding: 20px !important;
}

.or {
  margin: -10px 20px 20px 20px !important;
}
</style>



